import { AngularFireDatabase } from 'angularfire2/database';
import { Injectable } from '@angular/core';
import answer from '../src/app/answer';

const randrom = (data) => Math.floor(Math.random() * data.length) + 0;

@Injectable({
  providedIn: 'root',
})

export class ChatService {
  timerID;

  constructor(public db: AngularFireDatabase) {}

  getChatList() {
    return this.db.list('/chats').valueChanges();
  }

  routerChat(name: string) {
    return this.db.list(`/dialogues/${name}/dialogs`).valueChanges();
  }

  user(index: number) {
    return this.db.list(`/chats/${index}`).valueChanges();
  }

  addMessage(text: string, isBot: boolean, done: Function) {
    const dbLink = window.location.pathname.substr(1, );
    this.db.list(`/dialogues/${dbLink}/dialogs`).push({message: text, bot: isBot, time: new Date().toLocaleString()}).then(() => done());
    this.db.object(`/chats/${(+dbLink[4] - 1)}`).update({ lastMessage: text });
  }

  // автоответ бота
  botAnswer(done: Function) {
    clearTimeout(this.timerID);
    this.timerID = setTimeout(() => {
      const randomAnswer = answer[randrom(answer)];
      this.addMessage(`Ответ: ${randomAnswer}`, true, done);
    }, 1500);

  }

}
